.explode-bubble {
  animation: explode .6s forwards; }

.desplode-bubble {
  animation: desplode .6s forwards; }

@keyframes explode {
  0% {
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    background-color: rgba(255, 255, 255, 0.2); }
  100% {
    width: 1000px;
    height: 1000px;
    margin-left: -500px;
    margin-top: -500px;
    background-color: white; } }

@keyframes desplode {
  0% {
    width: 1000px;
    height: 1000px;
    margin-left: -500px;
    margin-top: -500px;
    background-color: rgba(255, 255, 255, 0.8); }
  100% {
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    background-color: rgba(255, 255, 255, 0.6); } }

@keyframes shake {
  10%,
  90% {
    transform: rotate(-6deg) translateX(-2px); }
  20%,
  80% {
    transform: rotate(3deg) translateX(-2px); }
  30%,
  50%,
  70% {
    transform: rotate(-6deg) translateX(-2px); }
  40%,
  60% {
    transform: rotate(3deg) translateX(-2px); } }

@keyframes blink {
  0% {
